import React,{ Component} from 'react';
import classes from '../assests/style.module.css';
import Task from '../component/Task.js'
class Todo extends Component {
    state = {
        input: '',
        task : []
    }
    inputHandler=(e) => {
        this.setState({input:e.target.value})
    }
    addTask = ()=>{
        if (this.state.input !== '') {
            this.setState({
                task : [...this.state.task,this.state.input],
                input : ''
            })
        }
    }
    deleteTask = (index)=>{
        let taskArray = [...this.state.task];
        taskArray.splice(index,1);
        this.setState({task:taskArray});
    }
    editTask = (event,index)=>{
        let taskArray = [...this.state.task];
        taskArray[index] = event.target.value;
        this.setState({task:taskArray});
    }
    render(){
        const task = this.state.task.map((taskName,index) => {
           return <Task 
           taskName={taskName} 
           key={index} 
           delete={()=>this.deleteTask(index)}
           edit={(event)=>this.editTask(event,index)}/>
        })
        return (
            <div className={classes.container}>
                <h1 className={classes.title}>Todo</h1>
                <input type="text" placeholder="Enter Task" onChange={this.inputHandler} value={this.state.input} className={classes.addField}/>
                <button className={classes.addBtn} onClick={this.addTask}>Add</button>
                {task}
            </div>
        );
    }
}
export default Todo;