import React from 'react';
import classes from '../assests/style.module.css';
import recycleBin from  '../assests/recycle-bin.png';

const Task = props => (
    <div className={classes.task}>
        <input type="text" onChange={props.edit} value={props.taskName}/>
        <img src={recycleBin} onClick={props.delete}/>
    </div>
)

export default Task;